<script>
import { defineComponent, h, ref, reactive } from 'vue'
import { mixinPosition, mixinBlock, mixinDense, mixinDisabled } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Checkbox',
  mixins: [
    ...addMixinPrefix([
      mixinPosition,
      mixinBlock,
      mixinDense,
      mixinDisabled
    ], 'radio')
  ],
  props: [
    'modelValue',
    'value'
  ],
  emits: ['update:modelValue'],
  setup(props, { slots, emit }) {
    const genContent = () => {
      return slots.default()
      // return h('div', {
      //   class: 'radio__content'
      // }, slots.default())
    }
    const changeValue = $event => {
      emit('update:modelValue', $event.target.value)
    }

    return {
      genContent,
      changeValue
    }
  },
  render() {
    const children = [
      this.$slots.default && this.genContent(),
      h('input', {
        type: 'radio',
        value: this.value,
        checked: this.$props.modelValue === this.$props.value,
        disabled: this.disabled,
        onInput: this.changeValue
      }),
      h('span', {
        class: 'radio__mark'
      })
    ]
    const listClass = [
      'radio',
      this.classPosition,
      this.classBlock,
      this.classDisabled,
      this.$props.modelValue ? 'radio-active' : ''
    ]

    return h('label', {
      class: listClass
    }, children)
  }
})
</script>